<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" :rules="rules" label-position="left" ref="form" label-width="90px" class="form mt20">
        <el-form-item class="item" label="年级/班级">
          <el-cascader
              class="input"
              clearable
              @change="changeClass"
              :options="classList"
          ></el-cascader>
        </el-form-item>
        <el-form-item class="item" label="科目" prop="courseId">
          <el-select default-first-option v-model="form.courseId" clearable filterable class="input">
            <el-option
              v-for="item in courseList"
              :label="item.name" :value="item.id" :key="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item class="item" label="时间" prop="inspectDate">
          <el-date-picker
            :picker-options="pickerOptions"
            v-model="form.inspectDate"
            type="date" class="input"
            placeholder="请选择时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item class="item" label="节次" prop="inspectSection">
          <el-input v-model="form.inspectSection" placeholder="请输入节次"></el-input>
        </el-form-item>
        <div class="clear"></div>
        <div class="separated-text mb25">任课老师课堂违纪情况</div>
        <el-form-item class="item" label="违纪时间点">
          <el-input v-model="form.teacherTiming" placeholder="请输入违纪时间点"></el-input>
        </el-form-item>
        <div class="clear"></div>
        <el-checkbox-group v-model="disciplineItem" class="mb20">
          <el-checkbox label="teacherTruant" border>旷课</el-checkbox>
          <el-checkbox label="teacherLate" border>迟到</el-checkbox>
          <el-checkbox label="teacherDimission" border>中途离岗</el-checkbox>
          <el-checkbox label="teacherPhone" border>接打手机</el-checkbox>
          <el-checkbox border>其它违纪情况</el-checkbox>
          <el-input v-model="form.teacherOther" class="checkbox-input" placeholder="请输入其它违纪情况"></el-input>
        </el-checkbox-group>
        <div class="separated-text mb25">学生课堂违纪情况</div>
        <el-form-item class="item" label="座位号">
          <el-input v-model="form.studentSeatNum" placeholder="请输入座位号"></el-input>
        </el-form-item>
        <el-form-item class="item" label="违纪时间点">
          <el-input v-model="form.studentTiming" placeholder="请输入违纪时间点"></el-input>
        </el-form-item>
        <div class="clear"></div>
        <el-checkbox-group v-model="disciplineS" class="mb20">
          <el-checkbox label="studentTalk" border>讲话</el-checkbox>
          <el-checkbox label="studentSleep" border>睡觉</el-checkbox>
          <el-checkbox label="studentSlapstick" border>下座位打闹</el-checkbox>
          <el-checkbox label="studentPlay" border>玩电子产品等</el-checkbox>
          <el-checkbox border>其它违纪情况</el-checkbox>
          <el-input v-model="form.studentOther" class="checkbox-input-s" placeholder="请输入其它违纪情况"></el-input>
        </el-checkbox-group>
      </el-form>
      <div class="btns">
        <div @click="doSubmit()" class="btn-submit  mr15">提交</div>
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
    </main-part>
  </div>

</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "课堂检查", path: "go(-1)"}, {name: "录入检查结果", path: ""}],
        // 违纪情况
        disciplineItem: [],
        // 学生违纪情况
        disciplineS: [],
        // 课程列表
        courseList: [],
        form: {
          courseId: null,
          gradeId: null,
          classId: null,
          inspectDate: null,
          inspectSection: null,
          teacherTruant: 'NO',
          teacherLate: 'NO',
          teacherDimission: 'NO',
          teacherPhone: 'NO',
          studentTalk: 'NO',
          studentSleep: 'NO',
          studentSlapstick: 'NO',
          studentPlay: 'NO',
          studentSeatNum: '',
          teacherTiming: '',
          studentTiming: '',
          teacherOther: '',
          studentOther: ''
        },
        rules: {
          inspectDate: {required: true, message: '请选择检查时间', trigger: 'change'},
          inspectSection: {required: true, message: '请输入节次', trigger: 'blur'},
          courseId: {required: true, message: '请选择科目', trigger: 'change'},
        },
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
        },
        // 年级班级级联
        classList: []
      }
    },
    methods: {
      // 获取所有的课程
      async getCourse() {
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do'
        })
        this.courseList = res
      },
      // 获取年级 班级 级联
      async getCascade(){
        let res = await this.$ajax({
          url: '/class/queryGradeClassCascade.do',
        })
        res.forEach(e=>{
          e.children.forEach(n=>{
            n.label = n.label.replace('初', 'C')
            n.label = n.label.replace('高', 'K')
          })
        })
        this.classList = res
      },
      // 切换班级
      changeClass(val){
        this.form.gradeId = val[0]
        this.form.classId = val[1]
      },
      // 提交
      async doSubmit(){
        let doIt = async  ()=> {
          let query = JSON.parse(JSON.stringify(this.form))
          // 违纪项
          this.disciplineItem.forEach(e=>{
            query[e] = 'YES'
          })
          // 学生违纪项
          this.disciplineS.forEach(e=>{
            query[e] = 'YES'
          })
          // 处理时间
          query.inspectDate = new Date(query.inspectDate).$Format('yyyy-MM-dd hh:mm:ss')

          await this.$ajax({
            url: '/assessInspectClassroom/addAssessInspectClassroom.do',
            el: '.btn-submit',
            noText: true,
            query,
          })
          this.$remind('录入成功')
          this.$router.push('/edu-affair/assessment/classroom')
        }

        await this.$refs.form.validate((valid) => {
          if (!valid) {
            this.$message.warning('请将必填项填写完整')
            return false
          } else {
            doIt()
          }
        })
      }
    },
    mounted(){
      this.getCourse()
      this.getCascade()
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .form
      @extend %clearfix
      .item
        float: left
        margin-right: 80px
        width: 420px
      .checkbox-input
        width: 340px
        margin-left: 10px
      .checkbox-input-s
        width: 295px
        margin-left: 10px
      .input
        width: 100%
    .btns
      margin-top: 15px
      text-align: center
</style>
